@import '../../styles/responsive';
@import '../../styles/variables';

.cardWrapper {
  // margin-top: 10px;
  // width: 334px;
  max-width: 280px;
  height: 154px;
  border-radius: 8px;
  padding: 14px 20px 14px 24px;

  @include mobile {
    min-width: 334px;
  }

  &.theme-light {
    background-color: $light-sidebar-bg;
  }
  &.theme-dark {
    background-color: $dark-sidebar-bg;
  }
  &.theme-violet {
    background-color: $violet-header-bg;
  }
  &.priorityClass-high {
    border-left: 4px solid rgba(190, 219, 176, 1);
  }
  &.priorityClass-medium {
    border-left: 4px solid rgba(224, 156, 181, 1);
  }
  &.priorityClass-low {
    border-left: 4px solid rgba(143, 161, 208, 1);
  }
  &.priorityClass-without {
    border-left: 4px solid rgb(128, 128, 128);
  }

  // @include pre-mobile {
  //   max-height: calc(100vh - 330px);
  // }
  // @include mobile {
  //   height: calc(100vh - 330px);
  // }

  // @include tablet {
  //   height: calc(100vh - 374px);
  // }
}

.cardTitle {
  margin-bottom: 8px;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  text-align: left;

  &.theme-light {
    color: $light-main-text;
  }
  &.theme-dark {
    color: $dark-main-text;
  }
  &.theme-violet {
    color: $violet-main-text;
  }
}

.cardDescription {
  font-weight: 400;
  font-size: 12px;
  margin-bottom: 14px;
  line-height: 16px;
  // max-width: 290px;
  // padding-bottom: auto;
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;

  &.theme-light {
    color: $light-secondary-text;
  }
  &.theme-dark {
    color: $dark-secondary-text;
  }
  &.theme-violet {
    color: $violet-secondary-text;
  }
}

.cardLine {
  display: flex;
  &.theme-light {
    border: 1px solid rgba(255, 255, 255, 0.1);
  }
  &.theme-dark {
    border: 1px solid rgba(255, 255, 255, 0.1);
  }
  &.theme-violet {
    border: 1px solid rgba(255, 255, 255, 0.1);
  }
}

.cardPriority {
  display: flex;
  gap: 12px;
}

.cardSubtitle {
  font-weight: 400;
  font-size: 8px;
  line-height: 12px;
  margin-top: 14px;

  &.theme-light {
    color: $light-main-text;
  }
  &.theme-dark {
    color: $dark-main-text;
  }
  &.theme-violet {
    color: $violet-main-text;
  }
}

.cardPrimaryIcon {
  display: flex;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: none;
  margin: 4px 0;
  &.priorityClass-high {
    background-color: rgba(190, 219, 176, 1);
  }
  &.priorityClass-medium {
    background-color: rgba(224, 156, 181, 1);
  }
  &.priorityClass-low {
    background-color: rgba(143, 161, 208, 1);
  }
  &.priorityClass-without {
    background-color: rgb(128, 128, 128);
  }
}

.cardSubtext {
  display: flex;
  align-items: center;
  gap: 5px;
  font-weight: 400;
  font-size: 10px;
  letter-spacing: -0.2px;

  // margin-bottom: 14px;
  &.theme-light {
    color: $light-main-text;
  }
  &.theme-dark {
    color: $dark-main-text;
  }
  &.theme-violet {
    color: $violet-main-text;
  }
}

.cardIcon {
  cursor: pointer;
  &.theme-light {
    color: $light-secondary-text;
    &:hover {
      color: $light-icon-hover;
    }
  }
  &.theme-dark {
    color: $dark-secondary-text;
    &:hover {
      color: $dark-icon-hover;
    }
  }
  &.theme-violet {
    color: $violet-secondary-text;
    &:hover {
      color: $violet-icon-hover;
    }
  }
}

.IconWrapper {
  /*  */
  // margin-left: 110px;
  margin-left: auto;
  align-items: flex-end;
  display: flex;
  gap: 8px;
}

.cardSubtextDeadline {
  font-weight: 400;
  font-size: 10px;
  // margin-bottom: 14px;
  margin-top: 4px;
  &.theme-light {
    color: $light-main-text;
  }
  &.theme-dark {
    color: $dark-main-text;
  }
  &.theme-violet {
    color: $violet-main-text;
  }
}
